window.bootstrap = require('bootstrap/dist/js/bootstrap.bundle.js');


window.document.addEventListener("DOMContentLoaded", function () {
    document.addEventListener('activate.bs.scrollspy', function (event) {
    const activeLink = document.querySelector('#tocSidebar .nav-link.active');

    if (!activeLink) return;

    // Expand all ancestor collapses
    let parent = activeLink.closest('.collapse');
    while (parent) {
      const p = parent?.parentElement?.querySelector('.nav-link');
      if (p){
        p.classList.add('active');
      }
      const bsCollapse = bootstrap.Collapse.getOrCreateInstance(parent);
      bsCollapse.show();
      parent = parent.parentElement.closest('.collapse');
    }

    // Optionally collapse siblings
    document.querySelectorAll('#tocSidebar .collapse.show').forEach(el => {
      if (!el.contains(activeLink)) {
        // el.classList.remove('active');
        const p = parent?.parentElement?.querySelector('.nav-link');
        if (p){
          p.classList.remove('active');
        }
        bootstrap.Collapse.getInstance(el)?.hide();
      }
    });
  });
});
